import {handleSubmit} from "~/partials/form_methods";

export default {
    props: {
        item: {
            type: Object | null,
        },
        value: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            is_visible: false,
            form: {
                id: null,
                name: null,
                email: null,
                password: null
            }
        }
    },

    watch: {
        value(v) {
            this.is_visible = v;
        }
    },
    methods: {
        setItem() {
            this.$set(this, 'form', this.item);
        },
        resetItem() {
            this.$set(this, 'form', {
                id: null,
                name: null,
                email: null,
                password: null
            });
        },
        handleSubmit(hide) {
            handleSubmit(this, hide, "/users/store", JSON.parse(JSON.stringify(this.form)));
        },
    }
};
